import React, { useState, useEffect } from 'react';
import { initializeApp } from 'firebase/app';
import { getDatabase, ref, onValue } from 'firebase/database';

// --- CRITICAL SECURITY WARNING: NEVER ADD ENCRYPTION KEYS HERE ---
//
// 1. Client-Side Code is Public: Any code in this file is downloaded and
//    accessible to anyone using the web browser's developer tools.
// 2. Key Compromise: If an encryption key (like an AES key) were placed
//    here, it would be immediately exposed, rendering any data encrypted
//    with it completely insecure.
// 3. Proper Encryption Location: If sensitive data needs to be encrypted
//    before being stored in Firebase, the encryption/decryption process
//    MUST occur on a secure backend (e.g., your Raspberry Pi Python script
//    or a Firebase Cloud Function) where the encryption key can be kept
//    absolutely secret and never exposed to the client.
//
// Your Firebase API Key below is public by design for client-side apps
// and does NOT need to be hidden. Data security is managed by Firebase
// Security Rules on the backend.
//
// --- END CRITICAL SECURITY WARNING ---

// Initialize Firebase with environment variables
const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN, // Added authDomain from env
  databaseURL: process.env.REACT_APP_FIREBASE_DATABASE_URL,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET, // Added storageBucket from env
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID, // Added messagingSenderId from env
  appId: process.env.REACT_APP_FIREBASE_APP_ID, // Added appId from env
};

// Optional: Basic validation to ensure keys are loaded
if (!firebaseConfig.apiKey) {
    console.error("Firebase API Key not found. Ensure .env file is configured correctly.");
    // You might want to display an error message in the UI or gracefully handle it
}


const app = initializeApp(firebaseConfig);
const db = getDatabase(app);

const TrafficInterface = () => {
  // State to hold the system status fetched from Firebase
  const [systemStatus, setSystemStatus] = useState({
    road1EV: "NO",
    road2EV: "NO",
    road1EV_ESP: "NO", // Assuming this is an ESP32-triggered priority flag
    road2EV_ESP: "NO", // Assuming this is an ESP32-triggered priority flag
    road1EV_green_timer: 0,
    road2EV_green_timer: 0,
    trafficlight1: { red: 0, yellow: 0, green: 0 }, // State for Road 1 lights
    trafficlight2: { red: 0, yellow: 0, green: 0 }, // State for Road 2 lights
    last_log: "",     // Last log message from the system (e.g., Python script)
    last_update: ""   // Timestamp of the last update from the system
  });

  // useEffect hook to set up Firebase listeners when the component mounts
  // and clean them up when the component unmounts.
  useEffect(() => {
    // Define all the specific Firebase paths we want to monitor
    const paths = [
      'road1EV',
      'road2EV',
      'road1EV_ESP',
      'road2EV_ESP',
      'road1EV_green_timer',
      'road2EV_green_timer',
      'trafficlight1_red',
      'trafficlight1_yellow',
      'trafficlight1_green',
      'trafficlight2_red',
      'trafficlight2_yellow',
      'trafficlight2_green',
      'last_log',
      'last_update'
    ];

    // Create a Firebase 'onValue' listener for each path
    // 'onValue' provides real-time updates as data changes in Firebase.
    const unsubscribeFunctions = paths.map(path => {
      const dbRef = ref(db, path); // Get a reference to the specific database path

      // Attach the listener. The callback function runs initially and on every data change.
      return onValue(dbRef, (snapshot) => {
        const value = snapshot.val(); // Get the data value from the snapshot

        // Update the component's state based on the received value
        setSystemStatus(prev => {
          // Special handling for traffic light color states (red, yellow, green)
          if (path.includes('trafficlight1_')) {
            const color = path.split('_')[1]; // Extract color (red, yellow, green)
            return { 
              ...prev, 
              trafficlight1: { ...prev.trafficlight1, [color]: value } 
            };
          } else if (path.includes('trafficlight2_')) {
            const color = path.split('_')[1]; // Extract color
            return { 
              ...prev, 
              trafficlight2: { ...prev.trafficlight2, [color]: value } 
            };
          } else {
            // For other paths, update the state property directly by its path name
            return { ...prev, [path]: value };
          }
        });
      }, (error) => {
        // --- Client-side error handling for Firebase read operations ---
        // This is crucial for debugging and providing user feedback if there's
        // a problem with fetching data (e.g., network issues, or misconfigured
        // Firebase Security Rules preventing reads).
        console.error(`Firebase read error for path ${path}:`, error);
        setSystemStatus(prev => ({
          ...prev,
          last_log: `[Frontend Error ${new Date().toLocaleTimeString('en-MY', { hour12: false })}] Failed to read ${path}: ${error.message}`
        }));
      });
    });

    // Cleanup function: This is returned by useEffect and runs when the
    // component unmounts. It detaches all Firebase listeners to prevent
    // memory leaks and unnecessary network activity.
    return () => unsubscribeFunctions.forEach(unsubscribe => unsubscribe());
  }, []); // Empty dependency array means this effect runs only once on mount and unmount

  // TrafficLight functional component for rendering individual traffic lights
  const TrafficLight = ({ road, lights }) => (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      margin: '20px',
      padding: '20px',
      border: '1px solid #ccc',
      borderRadius: '10px',
      boxShadow: '0 4px 8px rgba(0,0,0,0.1)' // Added a subtle shadow
    }}>
      <h3>Road {road}</h3>
      <div style={{
        width: '80px',
        height: '220px',
        backgroundColor: '#333',
        borderRadius: '10px',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-around',
        alignItems: 'center',
        padding: '10px'
      }}>
        {/* Red Light */}
        <div style={{
          width: '60px',
          height: '60px',
          borderRadius: '50%',
          backgroundColor: 'red',
          opacity: lights.red ? 1 : 0.3, // Full opacity if active (1), dim if inactive (0.3)
          border: '2px solid #000'
        }}></div>
        {/* Yellow Light */}
        <div style={{
          width: '60px',
          height: '60px',
          borderRadius: '50%',
          backgroundColor: 'yellow',
          opacity: lights.yellow ? 1 : 0.3,
          border: '2px solid #000'
        }}></div>
        {/* Green Light */}
        <div style={{
          width: '60px',
          height: '60px',
          borderRadius: '50%',
          backgroundColor: '#7CFC00', 
          opacity: lights.green ? 1 : 0.3,
          border: '2px solid #000'
        }}></div>
      </div>
      <div style={{ marginTop: '10px', textAlign: 'center' }}>
        <p>Green Timer: <strong>{road === 1 ? systemStatus.road1EV_green_timer : systemStatus.road2EV_green_timer}s</strong></p>
        <p>EV Priority: <strong>{road === 1 ? systemStatus.road1EV_ESP : systemStatus.road2EV_ESP}</strong></p>
      </div>
    </div>
  );

  // Main component render method
  return (
    <div style={{
      maxWidth: '1000px',
      margin: '0 auto',
      padding: '20px',
      fontFamily: 'Arial, sans-serif',
      backgroundColor: '#f9f9f9',
      minHeight: '100vh',
      boxSizing: 'border-box'
    }}>
      <h1 style={{ textAlign: 'center', color: '#333', marginBottom: '40px' }}>Traffic Control System (Live Monitoring)</h1>
      
      {/* Container for the two traffic light displays */}
      <div style={{
        display: 'flex',
        justifyContent: 'center',
        gap: '40px', // Space between the two traffic lights
        flexWrap: 'wrap' // Allow items to wrap on smaller screens
      }}>
        <TrafficLight road={1} lights={systemStatus.trafficlight1} />
        <TrafficLight road={2} lights={systemStatus.trafficlight2} />
      </div>
      
      {/* System Status Display */}
      <div style={{
        marginTop: '50px',
        padding: '30px',
        backgroundColor: '#fff',
        borderRadius: '10px',
        boxShadow: '0 4px 8px rgba(0,0,0,0.1)',
        borderLeft: '5px solid #007bff' // Added a subtle accent
      }}>
        <h2 style={{ color: '#007bff', marginBottom: '20px' }}>System Status</h2>
        <p style={{ fontSize: '1.1em', marginBottom: '10px' }}><strong>Last Update:</strong> {systemStatus.last_update}</p>
        <p style={{ fontSize: '1.1em', marginBottom: '20px', color: '#555' }}><strong>Last Log Message:</strong> <span style={{ fontFamily: 'monospace', backgroundColor: '#e9ecef', padding: '5px 8px', borderRadius: '4px' }}>{systemStatus.last_log || "No recent logs."}</span></p>
        
        {/* EV Detection Status */}
        <div style={{
          display: 'flex',
          gap: '20px',
          marginTop: '20px',
          flexWrap: 'wrap'
        }}>
          <div style={{
            padding: '12px 18px',
            backgroundColor: systemStatus.road1EV === 'YES' ? '#ffdfdf' : '#e9ecef', // Light red if YES
            color: systemStatus.road1EV === 'YES' ? '#dc3545' : '#333', // Darker text for active
            borderRadius: '8px',
            fontWeight: systemStatus.road1EV === 'YES' ? 'bold' : 'normal',
            border: `1px solid ${systemStatus.road1EV === 'YES' ? '#dc3545' : '#ccc'}`
          }}>
            Road 1 EV Detected: <strong>{systemStatus.road1EV}</strong>
          </div>
          <div style={{
            padding: '12px 18px',
            backgroundColor: systemStatus.road2EV === 'YES' ? '#ffdfdf' : '#e9ecef',
            color: systemStatus.road2EV === 'YES' ? '#dc3545' : '#333',
            borderRadius: '8px',
            fontWeight: systemStatus.road2EV === 'YES' ? 'bold' : 'normal',
            border: `1px solid ${systemStatus.road2EV === 'YES' ? '#dc3545' : '#ccc'}`
          }}>
            Road 2 EV Detected: <strong>{systemStatus.road2EV}</strong>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TrafficInterface;